
div#myLineup {
  display: block;
  max-width: 100%;
}

.lt-summary  {
      height:50px;
}

.lt-text {
  overflow : hidden;
  text-overflow : ellipsis;
  white-space : nowrap;
}

  
.tableWrap {
  display: block;
  overflow-x: scroll;
  overflow-y: hidden;
  border-bottom: 1px solid black;
}


    .resizer {
      display: inline-block;
      width: 10px;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      transform: translateX(50%);
      z-index: 1;
      touch-action:none;

      
    }
    
    .tableWrap   tr {
      text-align:center;
      margin-bottom:10px;
      font-size:small;
    }
    .tableWrap   .lt-bar {
      font-size:xx-small;
      
    }

    .tableWrap tr:hover { background-color: lightyellow; }
