text.pivot{
  fill: greenyellow;
}

rect.pivot{
  fill: none;
  stroke: greenyellow;
  stroke-width: 2
}

rect.notPivot{
  fill: none;
  stroke: none;
}

.foreground:hover{
  filter:brightness(0.6)
}

.backHover{
  fill: white
}

.backHover:hover{ 
  fill: dimgray
}

.backHoverClicked{
  fill: dimgray
}

.backHistoSmall{
  fill:rgba(10, 10, 10, 0.2)
}

.clicked{
  stroke:white;
  stroke-width:1.5;
  filter:brightness(0.4);
}

.drag{
  stroke: rgb(0, 140, 255);
  stroke-width: 5;
  cursor: e-resize;
}

.brush .selection{
  fill: yellowgreen;
  fill-opacity: .7;
  stroke: none;
}

.backHisto{
  fill:rgba(10, 10, 10, 0.637)
}

.pcp{
  fill:none;
  stroke: white;
  stroke-width: 1.5;
  opacity:0.5
}

text.dragging{
  fill:rgb(0, 140, 255)
}

#histogram{
  z-index:1
}

#line{
  position:absolute;
  z-index:2; 
  pointer-events:none;
}

#line_highlight{
  position:absolute;
  z-index:3; 
  pointer-events:none;
}

#line_filter{
  position:absolute;
  z-index:4; 
  pointer-events:none;
}

#line_filter_highlight{
  position:absolute;
  z-index:5; 
  pointer-events:none;
}

#others{
  z-index:4;
  position:absolute;
  pointer-events:none;
}

.line_hover path{
  fill:none;
  stroke:rgb(0, 195, 255)
}
