
.legend-container {
    flex: 1;
    display: flex;
    justify-content: flex-end;
  }
  
.legend-container .legend-element {
    padding: 0px 12px;
    margin-left: 6px;
    border-radius: 13px;
    display: flex;
    align-items: center;
    color:white;
    font-size:0.5em;
    text-align:center;
    stroke:lightgray;
    stroke-width:1px;
}