.none{
    background-color:white;
    word-wrap: break-all;
}

.ngram_0{
    background-color:white;
    color:gray;
    font-size:1.2em;
    word-wrap: break-all;
}

.ngram_1{
    background-color:rgb(144, 238, 144,0.5);
    font-size:1.2em;
    word-wrap: break-all;
}

.ngram_2{
    background-color:rgb(32,178,170,0.5);
    font-size:1.2em;
    word-wrap: break-all;

}
.ngram_3{
    background-color:rgb(135,206,250,0.5);
    font-size:1.2em;
    word-wrap: break-all;
}
.ngram_4{
    background-color:rgba(255,160,120,0.5);
    font-size:1.2em;
    word-wrap: break-all;
}

.legend-ngram {
    padding: 0px 12px;
    margin-left: 6px;
    border-radius: 13px;
    display: flex;
    align-items: center;
    color:black;
    font-size:0.6em;
    text-align:center;
    stroke:lightgray;
    stroke-width:1px;
}